.lib-button {
    width: 129px;
    height: 44px;
    border-radius: 5px;
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 16px;
    text-transform: uppercase;
    align-items: center;
    position: relative;
    overflow: hidden;
    text-align: center;
    color: var(--first-color);
    border: 1px solid var(--first-color);
    background-color: transparent;
    cursor: pointer;
    transition: All 250ms cubic-bezier(0.4, 0, 0.2, 1);

    &:not(:last-child) {
        margin-right: 16px;
    }

    &:active {
        top: .3em;
        left: .3em;
        box-shadow: 0 0 0 60px rgba(0, 0, 0, .05) inset;
    }

    &::before {
        content: "";
        height: 100px;
        position: absolute;
        transform: translate3d(-120%, -50px, 0) rotate3d(0, 0, 1, 45deg);
        transition: all 1.1s cubic-bezier(0.19, 1, 0.22, 1);
        width: 200px;
    }
  
    &:hover  {
        color: var(--first-color);
        background-color: var(--action-background-color);
        border: none;
        box-shadow: 0 0 5px rgba(255, 245, 245, 0.8);
    }

    &:hover::before {
        background-color: var(--first-color);
        transform: translate3d(100%, -100px, 0) rotate3d(0, 0, 1, 90deg);
    }
        @media screen and (min-width: 767px) {
            .lib-button {
                width: 129.53px;
    
                &:not(:last-child) {
                    margin-right: 19px;
                }
    
                &-watched {
                    padding: 0 11px;
                }
            }
        }
    
        @media screen and (min-width: 1023px) {
            .lib-button {
                width: 152px;
    
                &:not(:last-child) {
                    margin-right: 32px;
                }
            }
        }
}


  .disabled { 
    color: var(--first-color);
        background-color: var(--action-background-color);
        border: none;
        box-shadow: 0 0 5px rgba(255, 245, 245, 0.8);

  }
