.lib-button {
  width: 129px;
  height: 44px;
  text-transform: uppercase;
  text-align: center;
  color: var(--first-color);
  border: 1px solid var(--first-color);
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border-radius: 5px;
  align-items: center;
  font-family: Roboto;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 16px;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  position: relative;
  overflow: hidden;
}

.lib-button:not(:last-child) {
  margin-right: 16px;
}

.lib-button:active {
  top: .3em;
  left: .3em;
  box-shadow: inset 0 0 0 60px rgba(0, 0, 0, .05);
}

.lib-button:before {
  content: "";
  height: 100px;
  width: 200px;
  transition: all 1.1s cubic-bezier(.19, 1, .22, 1);
  position: absolute;
  transform: translate3d(-120%, -50px, 0)rotate3d(0, 0, 1, 45deg);
}

.lib-button:hover {
  color: var(--first-color);
  background-color: var(--action-background-color);
  border: none;
  box-shadow: 0 0 5px rgba(255, 245, 245, .8);
}

.lib-button:hover:before {
  background-color: var(--first-color);
  transform: translate3d(100%, -100px, 0)rotate3d(0, 0, 1, 90deg);
}

@media screen and (min-width: 767px) {
  .lib-button .lib-button {
    width: 129.53px;
  }

  .lib-button .lib-button:not(:last-child) {
    margin-right: 19px;
  }

  .lib-button .lib-button-watched {
    padding: 0 11px;
  }
}

@media screen and (min-width: 1023px) {
  .lib-button .lib-button {
    width: 152px;
  }

  .lib-button .lib-button:not(:last-child) {
    margin-right: 32px;
  }
}

.disabled {
  color: var(--first-color);
  background-color: var(--action-background-color);
  border: none;
  box-shadow: 0 0 5px rgba(255, 245, 245, .8);
}

