.backdrop {
  width: 100%;
  height: 100%;
  z-index: 1001;
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  background-color: rgba(0, 0, 0, .8);
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.is-hidden {
  visibility: visible;
