.container {
    width: 100%;
    padding-left: 20px;
    padding-right: 20px;
    margin-left: auto;
    margin-right: auto;

    @media screen and (min-width: 480px) {
        width: 480px;
    }

    @media screen and (min-width: 768px) {
        width: 768px;
        padding-left: 32px;
        padding-right: 32px;
        
    }

    @media screen and (min-width: 1280px) {
        width: 1280px;
    }
}

.section {
    padding-bottom:40px;
    @media screen and (min-width: 768px) {
    padding-bottom: 60px;
    }
}

.section__gallery{
    padding-top: 20px;
    @media screen and (min-width: 768px) {
    padding-top: 60px;
    }
}